@import url("https://fonts.googleapis.com/css2?family=Nabla&display=swap");
//font-family: 'Nabla', cursive;
@font-face {
  font-family: "Grobold";
  src: local("Open Sans"), url("./font/GROBOLD.ttf") format("truetype");
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html:has(.wrapper#dark-theme) {
  background: #323437;
  color: #fff;
}
html:has(.wrapper#light-theme) {
  background: #fff;
  color: #000;
}
.container {
  max-width: 1230px;
  padding: 0 15px;
  margin: 0 auto;
  margin-top: 80px;
  margin-bottom: 50px;
}
.wrapper#dark-theme {
  .card-item {
    background: #525252;
  }
  .settings .theme select {
    background: #000;
    color: #fff;
  }
  .App.blur {
    background: initial;
  }
  .addemail-popup,
  .addcard-popup,
  .editpopup {
    color: #fff;
    background: #000;
  }
  form {
    button,
    select,
    input {
      background: #ccc;
      color: #000;
    }
    button:hover {
      background: #323437;
      color: #ffffff;
    }
  }
  header {
    background: #7a7a7a;
  }
  .addcard-popup .btns {
    button.active {
      background: #323437;
      color: #fff;
    }
  }
  .card-item.upc {
    background: #ccc;
    color: #000;
  }
  .reg-link {
    color: #fff;
  }
  .login-btn {
    background: #000;
  }
}
