@import "../../mixin.scss";
@import "../../vars.scss";
.home-main {
  margin-top: 40px;
  .subscr-items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    row-gap: 25px;
    column-gap: 25px;
    margin-bottom: 20px;
    @media (max-width: 1000px) {
      grid-template-columns: 1fr 1fr;
    }
    @media (max-width: 660px) {
      grid-template-columns: 1fr;
      justify-items: center;
    }
  }
}
.upcoming,
.subscr {
  .nav {
    @include flexsb;
    margin-bottom: 30px;
    a {
      text-decoration: none;
      color: #fff;
      padding: 5px 10px;
      background: #000;
    }
  }
}
.upcoming {
  margin-bottom: 40px;
  .upcoming-items {
    display: flex;
    align-items: center;
    overflow-x: auto;
    padding-bottom: 15px;
    @include scrollbar-style;
  }
  .card-item {
    height: 130px;
    min-width: 150px;
    width: 150px;
    padding: 20px;
    margin-left: 25px;
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    &:has(.confirm) {
      width: 220px;
      min-width: 220px;
    }

    .confirm {
      border: transparent;
      background: transparent;
      margin-top: auto;
      @include transition;
      position: relative;
      z-index: 2;
      cursor: pointer;
      &::after {
        opacity: 0;
        position: absolute;
        width: 22px;
        height: 22px;
        background: $accentcolor;
        bottom: 9px;
        left: 0;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        content: "";
        z-index: -1;
      }
      &::before {
        position: absolute;
        width: 42px;
        height: 18px;
        background: #ebc103;
        bottom: 0px;
        top: 8px;
        left: 7px;
        opacity: 0;
        content: "";
        z-index: -1;
      }

      &:hover {
        &::before,
        &::after {
          opacity: 1;
        }
      }
      img {
        width: 50px;
        height: 50px;
      }
    }
    &:first-child {
      margin-left: 0;
    }
    .info {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 100%;
      .price-block {
        margin: 0;
        position: absolute;
        bottom: 20px;
      }
    }

    .info-data {
      display: flex;
      justify-content: space-between;
      flex-direction: column;
      height: 100%;
    }
  }
}
