@import "../../mixin.scss";
@import "../../vars.scss";

.accountpage {
  margin: 0 auto;
  margin-top: 80px;
  max-width: 600px;

  .settings {
    display: flex;
    flex-direction: column;
    div {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin: 10px 0;
      span {
        font-size: 17px;
      }
      button {
        @include button-submit;
        width: 85px;
        margin: 0;
        height: 30px;
      }
    }
    .editnu {
      display: flex;
      align-items: center;
      position: relative;
      button {
        font-size: 16px;
        outline: none;
        position: absolute;
        right: 1px;
        width: 40px;
        height: 28px;
        background: $accentcolor;
        border-radius: 0 15px 15px 0;
        -webkit-border-radius: 0 15px 15px 0;
        -moz-border-radius: 0 15px 15px 0;
        -ms-border-radius: 0 15px 15px 0;
        -o-border-radius: 0 15px 15px 0;
      }
      input {
        height: 30px;
        outline: none;
        border: 1px solid #000;
        @include border-radius;
        padding: 5px 30px 5px 5px;
        &:focus {
          background: rgba($color: #fff, $alpha: 1);
          color: #000;
        }
      }
    }
    .logout {
      cursor: pointer;
      width: 300px;
      height: 35px;
      margin: 15px auto;
      background: #fff;
      font-size: 16px;
      outline: none;
      font-weight: 500;
      @include transition;
      border: 1px solid #000;
      &:hover {
        background: #000000;
        color: #ffffff;
        border: 1px solid #ffffff;
      }
      @media (max-width: 660px) {
        width: 200px;
      }
    }
  }
  select {
    outline: none;
    height: 30px;
    @include flexc;
    width: 100px;
  }
  details {
    outline: none;
  }
  summary {
    font-size: 19px;
    font-weight: 500;
    margin-bottom: 10px;
  }
  .email-card {
    @include border-radius;
    padding: 5px;
    border: 1px solid #ccc;
    button.del-email {
      outline: none;
      width: 50px !important;
      background: none;
      border: none;
      height: 20px;
      margin-left: 20px !important;
      &:hover {
        background: none;
        border: none;
      }
    }
    .home-email {
      @include border-radius;
      background: #000;
      padding: 5px;
      width: 30px;
      height: 30px;
      margin: 0 11px 0 28px;
    }
    span {
      font-weight: 500;
      margin-left: 10px;
    }
  }
  .statistic {
    margin: 0 0 0 auto !important;
    p {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  .home-icon {
    margin: 0 17px 0 20px;
  }
}
