@import "../../mixin.scss";
@import "../../vars.scss";
.navbar {
  display: flex;
  align-items: center;
  position: relative;
  .navbar-items {
    padding: 10px 0;
    display: flex;
    align-items: center;
    margin-right: 70px;
    margin-left: 45px;
    overflow-x: auto;
    @include scrollbar-style;
  }
  button.addpopup {
    top: 10px;
    position: absolute;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #000;
    min-width: 35px;
    width: 35px;
    height: 35px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    outline: none;
    cursor: pointer;
    transform: translate(-50%);
    -webkit-transform: translate(-50%);
    -moz-transform: translate(-50%);
    -ms-transform: translate(-50%);
    -o-transform: translate(-50%);
    img {
      width: 20px;
      height: 20px;
    }
    border: 2px solid transparent;
    &:hover{
      border: 2px solid $accentcolor;
    
    }
  }
}
button.home {
  top: 10px;
  position: absolute;
  left: 10px;
  min-width: 40px;
  width: 40px;
  height: 40px;
  outline: none;
  border: transparent;
  cursor: pointer;
  transform: translate(-50%);
  -webkit-transform: translate(-50%);
  -moz-transform: translate(-50%);
  -ms-transform: translate(-50%);
  -o-transform: translate(-50%);
  background: #000;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  img {
    width: 20px;
    height: 20px;
  }
}
.navbar-item {
  cursor: pointer;
  width: fit-content;
  background: #7a7a7a;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 20px;
  border: 1px solid #000;
  @include border-radius;
  @include transition;
  margin-left: 20px;
  color: #fff;
  &:first-child {
    margin-left: 0;
  }
  &:hover {
    background: #3d3d3d;

  }
  &.active {
    background: #000;
  }
  svg {
    margin-right: 10px;
  }
}
