@import "./vars";
@mixin border-radius {
  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
}
@mixin box-shadow {
  -webkit-box-shadow: 0px 0px 10px 5px rgba(148, 148, 148, 0.75);
  -moz-box-shadow: 0px 0px 10px 5px rgba(148, 148, 148, 0.75);
  box-shadow: 0px 0px 10px 5px rgba(148, 148, 148, 0.75);
}
@mixin transition {
  transition: all 0.3s ease-in;
  -webkit-transition: all 0.3s ease-in;
  -moz-transition: all 0.3s ease-in;
  -ms-transition: all 0.3s ease-in;
  -o-transition: all 0.3s ease-in;
}
@mixin font($size) {
  font-size: $size;
  line-height: 1.2;
  font-weight: 400;
  letter-spacing: -0.022em;
}
@mixin flexc {
  display: flex;
  justify-content: center;
  align-items: center;
}
@mixin flexsb {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@mixin popup-style {
  z-index: 10;
  background: #ececec;
  position: absolute;
  top: 33%;
  left: 50%;
  border: 1px solid #888888;
  padding: 25px;
  width: 500px;
  height: auto;
  transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -o-transform: translateX(-50%);
  @media (max-width: 660px) {
    width: 60%;
    min-width: 290px;
    padding: 10px;
  }
}
@mixin label-style {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  margin-bottom: 15px;
  font-size: 18px;
}
@mixin input-style {
  outline: none;
  padding: 8px;
  font-size: 16px;
  width: 100%;
  margin-top: 5px;
  &:focus {
    border: 2px solid #000;
  }
}
@mixin input-checkbox-style {
  width: 25px;
  height: 25px;
}
@mixin button-submit {
  @include flexc;
  outline: none;
  border: transparent;
  cursor: pointer;
  width: 150px;
  background: #000;
  height: 35px;
  color: #fff;
  margin: 0 auto;
  font-size: 16px;
  @include transition;
  border: 1px solid transparent;
  &:hover {
    background: #ffffff;
    color: #000;
    border: 1px solid #000;
  }
}
@mixin scrollbar-style {
  &::-webkit-scrollbar {
    width: 5px;
    height: 7px;
  }

  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey;
    @include border-radius;
  }

  &::-webkit-scrollbar-thumb {
    background: #86eb03;
    @include border-radius;
  }
}
