@import "../../mixin.scss";
@import "../../vars.scss";
header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: #fff;
  z-index: 12;
  .logo{
    img{
      width: 35px;
      height: 35px;

    }
  }
  nav {
    ul {
      display: flex;
      justify-content: space-around;
      align-items: center;
      padding: 10px;
      border-bottom: 1px solid #000;
      li {
        list-style: none;
        a {
          padding: 5px 10px;
          text-decoration: none;
          color: #000;
          font-weight: 500;
          text-transform: uppercase;
          display: flex;
          justify-content: center;
          align-items: center;
          &.active {
            color: #000ccc;
          }
        }
      }
      .links{
        display: flex;
        align-items: center;
      }
    }
  }
}
