@import "../../mixin.scss";
@import "../../vars.scss";

.card-item {
  position: relative;
  padding: 15px;
  width: 300px;
  height: auto;
  border: 1px solid #ccc;
  background: #e2e2e2;
  display: flex;
  flex-direction: column;
  @include border-radius;
  @media (max-width: 660px) {
    min-width: 300px;
    width: 100%;
    max-width: 500px;
  }
  .price-block {
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .daysleft {
    padding-top: 10px;
  }
  .btn-block {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    button {
      outline: none;
      padding: 5px;
      border: none;
      background: none;
      cursor: pointer;
      img {
        width: 20px;
        height: 20px;
      }
    }
  }

  h4 {
    font-size: 20px;
  }
  span.period {
    font-family: $font-accent;
    top: 5px;
    right: 15px;
    display: block;
    font-size: 23px;
    font-weight: 700;
    text-transform: uppercase;
    position: absolute;
  }
}
